'use client'

import { useState } from 'react';
import { ArrowUpTrayIcon } from '@heroicons/react/24/outline';
import customersCrud from '@/app/offline/crud/Customers';
import { toast } from 'react-toastify';

export default function RestorePage() {
  // Sample data - replace with actual data later
  const [backupData] = useState({
    bills: [
      { id: 1, date: '2024-03-20', total: 2500, status: 'Completed' },
      { id: 2, date: '2024-03-20', total: 1800, status: 'Pending' },
      { id: 3, date: '2024-03-19', total: 3200, status: 'Completed' },
    ],
    inventory: [
      { id: 1, item: 'Rice', quantity: 50, unit: 'kg', lastUpdated: '2024-03-20' },
      { id: 2, item: 'Oil', quantity: 25, unit: 'L', lastUpdated: '2024-03-19' },
      { id: 3, item: 'Wheat', quantity: 30, unit: 'kg', lastUpdated: '2024-03-18' },
    ],
    customers: [
      { id: 1, name: 'John Doe', visits: 15, lastVisit: '2024-03-20' },
      { id: 2, name: 'Jane Smith', visits: 8, lastVisit: '2024-03-19' },
      { id: 3, name: 'Mike Johnson', visits: 12, lastVisit: '2024-03-18' },
    ]
  });

  const handleCustomerRestore = async () => {
    try {
      const result = await customersCrud.syncFromServer();
      if (result.returncode === 200) {
        toast.success('Bill printed successfully');
      }
    } catch (error) {

    }
  };

  return (
    <div className="p-6">
      <h1 className="text-2xl font-bold mb-6">Data From Server</h1>

      {/* Customers Section */}
      <div className="mb-8">
        <div className="flex justify-between items-center mb-4">
          <h2 className="text-xl font-semibold">Customers</h2>
          <div className="space-x-2">
            <button
              onClick={() => handleCustomerRestore()}
              className="bg-blue-500 text-white px-4 py-2 rounded flex items-center"
            >
              <ArrowUpTrayIcon className="h-5 w-5 mr-2" />
              Restore
            </button>
          </div>
        </div>
        <div className="bg-white rounded-lg shadow overflow-x-auto">
          <table className="min-w-full">
            <thead className="bg-gray-50">
              <tr>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">ID</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Name</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Total Visits</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Last Visit</th>
              </tr>
            </thead>
            <tbody className="bg-white divide-y divide-gray-200">
              {backupData.customers.map((customer) => (
                <tr key={customer.id}>
                  <td className="px-6 py-4 whitespace-nowrap">{customer.id}</td>
                  <td className="px-6 py-4 whitespace-nowrap">{customer.name}</td>
                  <td className="px-6 py-4 whitespace-nowrap">{customer.visits}</td>
                  <td className="px-6 py-4 whitespace-nowrap">{customer.lastVisit}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
}
